import { api } from "../lib/utils/api";
import siteConfig from "../config/siteConfig";

function login(username , password){
    const requestOption = {
        url : "/auth",
        method : "post",
        data : JSON.stringify({username , password})
    }

    return api(requestOption).then(result=>{
        localStorage.setItem(siteConfig.tokenLabel , JSON.stringify(result?.data?.token));
        return result;
    })
}


function logOut(){
    localStorage.removeItem(siteConfig.tokenLabel);
}


export const authService = {
    login,
    logOut
}