
import _app from "./_app";
import Swal from "sweetalert2";


const convertErrorsToHtml = (errors, htmlType) => {
  let html = ``;
  if (Array.isArray(errors) && errors.length > 0) {
    errors.map((err, i) => {
      html += `<ul class="callout callout-${
        err?.key == "error" ? "danger" : err.key != null ? err.key : "danger"
      }"> <li> ${err?.title ? `<div>${err.title}</div>` : ""}${
        err?.description ? err.description : "istek sırasında bir hata oluştu"
      }</li>
      </ul>`;
    });
  } else {
    html = `<ul class="callout callout-${
      errors?.key == "error"
        ? "danger"
        : errors?.key != null
        ? errors.key
        : "danger"
    }"> <li> ${errors?.title ? `<div>${errors.title}</div>` : ""}${
     ( errors?.description
        ? errors.description + " <b>("+errors.code+")</b>"
        : "istek sırasında bir hata oluştu")
    }</li>
   
    </ul>`;
  }
  return html;
};

const ErrorProvider = ({response}) => {
  console.log(response)
  let { data } = response;

  console.info("data");
  console.info(data);
  var icon = Array.isArray(data)
    ? "info"
    : typeof data === "object"
    ? data?.key
    : "info";

  var html = convertErrorsToHtml(data);
  console.info("icon_____!");
  console.info(icon);
  Swal.fire({
    title: Array.isArray(data)
      ? "Uyarı"
      : typeof data === "object"
      ? data?.title
      : "Uyarı",
    icon: icon,
    html: html,
    showConfirmButton: false,
    showCancelButton: true,
    cancelButtonText: "Tamam",
  });
};

export const ErrorProviderCallout = ({response}) => {
  if(!response) return;
  let { data , status} = response;
  var html = convertErrorsToHtml(data);

  if(status === 403){
    var y = document.getElementById("site-content");
    console.info(y);
    if(!y) return;
    y.innerHTML = `${html}`;
    return;  
  }

  var x = document.getElementById("error-content");
  if(!x) return;
  x.innerHTML = `${html}`;
 
};

export default ErrorProvider;